import { Box, Grid, styled, Typography, useTheme } from '@mui/material';
import { GatsbyImage, type IGatsbyImageData } from 'gatsby-plugin-image';
import type { ReactNode } from 'react';
import React from 'react';

interface Props {
  title: string;
  thumbnail?: IGatsbyImageData;
  subTitle: string;
  date?: string;
  badge?: ReactNode;
  onClick?: () => void;
}
const PostCard = ({ title, thumbnail, subTitle, date, badge, onClick }: Props) => {
  const { spacing, breakpoints, typography, palette } = useTheme();

  return (
    <Box
      onClick={onClick}
      sx={{
        padding: `${spacing(2)} ${spacing(1.5)}`,
        color: palette.text.primary,
      }}
    >
      <Grid container spacing={2} alignItems="center">
        <Grid
          item
          xs={12}
          md
          sx={{
            order: 2,
            [breakpoints.up('md')]: {
              order: 1,
            },
          }}
        >
          <Box>
            {badge != null ? <Box sx={{ marginBottom: spacing(1) }}>{badge}</Box> : null}
            <Typography
              gutterBottom
              variant="body1"
              component="h2"
              fontWeight={typography.fontWeightBold}
              lineHeight={1.35}
              letterSpacing="-0.016em"
              fontSize="clamp(1.5rem, 2vw, 1.42rem)"
              sx={{
                marginBottom: spacing(1),
              }}
            >
              {title}
            </Typography>
            <Typography
              gutterBottom
              component="p"
              variant="body2"
              fontFamily={`"Pretendard Variable", Pretendard, "Helvetica Neue", Arial, sans-serif`}
              fontSize="clamp(0.92rem, 1.2vw, 1rem)"
              lineHeight={1.6}
              color={palette.mode === 'light' ? palette.grey[700] : palette.grey[400]}
              sx={{
                display: '-webkit-box',
                overflow: 'hidden',
                WebkitLineClamp: 3,
                WebkitBoxOrient: 'vertical',
                marginBottom: spacing(4),
              }}
            >
              {subTitle}
            </Typography>
            <Typography
              variant="caption"
              component="p"
              fontFamily={`"Pretendard Variable", Pretendard, "Helvetica Neue", Arial, sans-serif`}
              fontWeight={typography.fontWeightRegular}
              fontSize="0.92rem"
              letterSpacing="0.01em"
              color={palette.text.secondary}
              sx={{ marginTop: 'auto' }}
            >
              {date}
            </Typography>
          </Box>
        </Grid>
        <Grid
          item
          xs={12}
          md="auto"
          sx={{
            order: 1,
            [breakpoints.up('md')]: {
              order: 2,
            },
          }}
        >
          <Box
            sx={{
              position: 'relative',
              width: '100%',
              paddingBottom: '54%',
              [breakpoints.up('md')]: {
                width: 176,
                paddingBottom: '68%',
              },
            }}
          >
            {thumbnail && <ThumbnailImage image={thumbnail} alt={title} />}
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

const ThumbnailImage = styled(GatsbyImage)(({ theme }) => ({
  position: 'absolute',
  objectFit: 'cover',
  width: '100%',
  height: '100%',
  borderRadius: theme.shape.borderRadius,
}));

export default PostCard;
