import React from 'react';
import { graphql, HeadProps, PageProps } from 'gatsby';
import CategoryPage from 'containers/CategoryPage';
import SEO from 'components/SEO';
import { slugify } from '../../utils/slugify';
import { getAlternateLanguages } from 'utils/i18nPath';
import type { SupportedLanguages } from 'models/languages';

const CategoryTemplate = (
  props: PageProps<Queries.CategoryPageQuery, { category: string; lang: string }>,
) => {
  return <CategoryPage {...props} />;
};

export default CategoryTemplate;

export const Head = ({
  pageContext,
  location,
}: HeadProps<Queries.CategoryPageQuery, { category: string; lang: string }>) => {
  const slug = slugify(pageContext.category);
  const alternateLanguages = getAlternateLanguages(pageContext.lang as SupportedLanguages, `/categories/${slug}/`);

  return (
    <SEO
      title={pageContext.category}
      description={`Posts in category "${pageContext.category}"`}
      lang={pageContext.lang}
      pathname={location.pathname}
      alternateLanguages={alternateLanguages}
    />
  );
};

export const pageQuery = graphql`
  query CategoryPage($category: String!, $lang: String!) {
    allMarkdownRemark(
      filter: {
        fields: { mdType: { eq: "posts" }, lang: { eq: $lang } }
        frontmatter: { categories: { in: [$category] } }
      }
      sort: { frontmatter: { date: DESC } }
    ) {
      edges {
        node {
          ...Post
        }
      }
    }
  }
`;
