import { Grid, useTheme } from '@mui/material';
import PostCard from 'components/cards/PostCard';
import Layout from 'components/Layout';
import RowList from 'components/RowList';
import { Link, type PageProps } from 'gatsby';
import React, { useMemo } from 'react';

const CategoryPage = ({
  data,
  pageContext,
}: PageProps<Queries.CategoryPageQuery, { category: string; lang: string }>) => {
  const theme = useTheme();
  const posts = useMemo(() => data.allMarkdownRemark.edges.map(e => e.node), [data]);

  return (
    <Layout title={<Layout.Title title={pageContext.category} description="" />}>
      <RowList>
        {posts.map(p => {
          const path = p.fields?.path ?? '/';
          const title = p.frontmatter?.title ?? '';
          const subTitle = p.frontmatter?.subTitle ?? '';
          const thumbnail = p.frontmatter?.thumbnail?.childImageSharp?.gatsbyImageData;
          const date = p.frontmatter?.date ?? '';

          return (
            <Grid
              item
              xs={12}
              key={p.id}
              sx={{
                padding: `${theme.spacing(0.25)} 0`,
                '& a': {
                  textDecoration: 'none',
                  display: 'block',
                  borderRadius: theme.shape.borderRadius * 2,
                  transition: 'background-color 0.2s ease-in-out',
                },
                '& a:hover': {
                  backgroundColor:
                    theme.palette.mode === 'light' ? 'rgba(0, 0, 0, 0.01)' : 'rgba(255, 255, 255, 0.025)',
                },
                '& + &': {
                  borderTop: `1px solid ${theme.palette.divider}`,
                },
              }}
            >
              <Link to={path}>
                <PostCard title={title} subTitle={subTitle} thumbnail={thumbnail} date={date} />
              </Link>
            </Grid>
          );
        })}
      </RowList>
    </Layout>
  );
};

export default CategoryPage;
